.hero {
    position: relative;
    background-color: var(--overlay); /* Adjust this if needed */
    margin: auto;
    padding: 8rem 1rem 0 1rem;
    width: 100%;
    color: #f8f8f8;
}

.hero:before {
    content: '';
    /* background: url('./images/hero.jpg') no-repeat center center/cover; */ /* Commented out */
    background-color: rgba(0, 0, 0, 0.781); /* Set background to black */
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
    /* mix-blend-mode: multiply; */ /* Commented out as it's not needed for a solid color */
}
.hero .container img {
    width: 600px; /* Default width for larger screens */
    height: auto;
    padding: 0rem;
}


.hero .container {
    width: 1240px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
}

.hero h2 {
    font-size: 3rem;
}

.hero .col2b {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 3rem;
}

.hero .col2b p:first-of-type {
    margin: 2rem 0;
}

.hero .col2b p:nth-child(4) {
    font-size: 1.8rem;
    font-style: italic;
    font-weight: 600;
}

.hero button {
    margin-top: 1rem;
}

@media screen and (max-width:940px) {
    .hero .container {
        grid-template-columns: 1fr;
        width: 100%;
        padding: 0 10px; /* Add some padding */
    }

    .hero .container {
        width: 100%;
    }

    .hero .container img {
        max-width: 300px;
        width: 90vw;
    }

    .hero .container .col2b {
        padding: 0;
    }

    .hero button {
        margin-bottom: 1rem;
    }
}

/* In your .css file */
.resized-image {
    width: 200px; /* or any desired width */
    height: auto; /* maintains aspect ratio */
}

